body
  font-family: 'Roboto Slab', serif
  font-style: normal
  font-weight: 400
  font-size: 16px
  line-height: 22px
  color: $color-text

  width: 100%
  height: 100vh
  @include section-bg( $color-bg, )

h2
  font-family: 'Road Rage'
  font-style: normal
  font-weight: 400
  letter-spacing: 0.1em

.login
  display: flex
  justify-content: center
  align-items: center
  height: 100vh
  &__box
    position: relative
    width: 380px
    height: 420px
    background: $color-bg-box
    border-radius: 8px
    overflow: hidden
    &::before
      content: ''
      position: absolute
      top: -50%
      left: -50%
      width: 380px
      height: 420px
      background: linear-gradient(0deg, transparent, transparent, #2EFE22 , #2EFE22, #2EFE22)
      z-index: 1
      transform-origin: bottom right
      animation: aminate 6s linear infinite
    &::after
      content: ''
      position: absolute
      top: -50%
      left: -50%
      width: 380px
      height: 420px
      background: linear-gradient(0deg, transparent, transparent, #2EFE22 , #2EFE22, #2EFE22)
      z-index: 1
      transform-origin: bottom right
      animation: aminate 6s linear infinite
      animation-delay: -3s
  &__border-line
    position: absolute
    top: 0
    inset: 0
    &::before
      content: ''
      position: absolute
      top: -50%
      left: -50%
      width: 380px
      height: 420px
      background: linear-gradient(0deg, transparent, transparent, #FFBD26 , #FFBD26, #FFBD26)
      z-index: 1
      transform-origin: bottom right
      animation: aminate 6s linear infinite
      animation-delay: -1.5s
    &::after
      content: ''
      position: absolute
      top: -50%
      left: -50%
      width: 380px
      height: 420px
      background: linear-gradient(0deg, transparent, transparent, #FFBD26 , #FFBD26, #FFBD26)
      z-index: 1
      transform-origin: bottom right
      animation: aminate 6s linear infinite
      animation-delay: -4.5s

.form
  position: absolute
  inset: 4px
  background: $color-bg-form
  border-radius: 8px
  padding: 50px 32px
  z-index: 2
  display: flex
  flex-direction: column
  &__title
    font-size: 64px
    line-height: 70px
    text-align: center
  &__input-box
    position: relative
    width: 300px
    margin-top: 35px
  &__input
    position: relative
    width: 100%
    padding: 20px 10px 10px 10px
    background: transparent
    outline: none
    border: none
    color: #8E676A
    font-size: 1em
    transition: 0.5s
    z-index: 10
    &:valid ~ .form__label,
    &:focus ~ .form__label
      color: $color-link
      font-size: 0.85em
      transform: translateY(-34px)
    &:valid ~ i,
    &:focus ~ i
      height: 44px
  &__label
    position: absolute
    left: 0
    padding: 20px 0 10px 0
    pointer-events: none
    color: $color-text
    font-size: 1em
    transition: 0.5s
  i
    position: absolute
    left: 0
    bottom: 0
    width: 100%
    height: 2px
    background: $color-text
    border-radius: 4px
    overflow: hidden
    transition: 0.5s
    pointer-events: none
  &__links
    display: flex
    justify-content: space-between
  &__link
    color: $color-link
    margin: 10px 0
    &:hover, &:nth-child(2)
      color: $color-text
    &:nth-child(2):hover
      color: $color-link
  &__btn
    outline: none
    padding: 9px 25px
    border: 2px solid $color-text
    background: transparent
    cursor: pointer
    font-size: 0.9em
    color: $color-text
    border-radius: 4px
    width: 100px
    margin-top: 10px
    &:active
      opacity: 0.8
    &:hover
      box-shadow: 0px 4px 4px rgba(194, 164, 212, 0.25)

@media screen and (max-width: 480px)
  .login
    &__box
      width: 96%
      min-width: 300px
  .form
    padding: 35px 15px
    &__input-box
      width: 100%
    &__link
      margin: 16px 0
    &__btn
      width: 100%
      margin-top: 20px
